            this.Re.push(this.I2[index] * this.I2[index - 1] + this.Q2[index] * this.Q2[index - 1]);
            this.Im.push(this.I2[index] * this.Q2[index - 1] - this.Q2[index] * this.I2[index - 1]);

            this.Re[index] = 0.2 * this.Re[index] + 0.8 * this.Re[index - 1];
            this.Im[index] = 0.2 * this.Im[index] + 0.8 * this.Im[index - 1];

            this.period.push(0);
            if (this.Im[index] !== 0.0 && this.Re[index] !== 0.0) this.period[index] = 360 / (Math.atan(this.Im[index] / this.Re[index]) * (180.0 / 3.14159265359));
            if (this.period[index] > 1.5 * this.period[index - 1]) this.period[index] = 1.5 * this.period[index - 1];
            if (this.period[index] < 0.67 * this.period[index - 1]) this.period[index] = 0.67 * this.period[index - 1];
